.Carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 70px;
}

.legend {
  opacity: .5;
}

.eventsContainer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  box-sizing: border-box;
  border: 10px 20px 10px 20px;
}

.singleEvent {
  background-color: #f0f3f8e1;
  box-shadow: 1px 4px 8px 1px rgba(0, 0, 0, 0.2);
  margin: 10px;
  padding: 10px;
  border-radius: 1em;
  font-size: 20px;
  position: relative;
  text-align: center;
}

span {
  color: #3686e0
}

img.image {
  height: 150px;
}

.head {
  background: rgba(157, 187, 63, .85);
  padding: 5px;
  color: #ffffef;
  text-transform: uppercase;
  font-family: Comic Sans Ms;
}
